import React from 'react';
import LoadStatus from '../../utilities/load-status.jsx';
import ErrorMessage from '../../utilities/error-message.jsx';
import Checkbox from '../pieces/checkbox.jsx';
import PropTypes from 'prop-types';

class RegisterForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      username: '',
      password: '',
      agreement: false
    };
    this.submitForm = this.submitForm.bind(this);
    this.fillForm = this.fillForm.bind(this);
    this.getBetterChildren = this.getBetterChildren.bind(this);
  }

  getBetterChildren(children) {
    return React.Children.map(children, (child) => {
      if (child.props.children) {
        child = React.cloneElement(child, {
          children: this.getBetterChildren(child.props.children)
        });
      }
      if (['input', 'textarea', 'select', 'option', 'button', 'datalist', 'keygen', 'output'].indexOf(child.type) >= 0) {
        return React.cloneElement(child, {
          onChange: this.fillForm
        })
      } else {
        return child;
      }
    });
  }

  submitForm(e) {
    e.preventDefault();
    e.stopPropagation();
    this.props.onSubmit(this.state);
  }

  fillForm(e) {
    switch (e.currentTarget.id) {
      case ('username'):
        this.setState({ username: e.currentTarget.value });
        break;
      case ('name'):
        this.setState({ name: e.currentTarget.value });
        break;
      case ('password'):
        this.setState({ password: e.currentTarget.value });
        break;
      case ('confirm-password'):
        this.setState({ confirm_password: e.currentTarget.value });
        break;
      case ('agreement'):
        this.setState({ agreement: e.currentTarget.checked });
        break;
      default:
        const newStateObj = {};
        newStateObj[e.currentTarget.id.replace('-', '_')] = e.currentTarget.type === 'checkbox' ? e.currentTarget.checked : e.currentTarget.value;
        this.setState(newStateObj);
        break;
    }
  }

  render() {
    let error = '';
    if (this.props.error && this.props.error.type) {
      error = this.props.error;
    } else if (this.props.error && this.props.error.message) {
      error = (
        <ErrorMessage
          error={this.props.error}
          theme={this.props.error.theme || this.props.errorTheme}
        />
      );
    }
    
    return (
      <div className="register-box">
        <div className="register-logo">
          <a
            href={this.props.logoLink}
            onClick={this.props.onLogoClick ? e => {
              e.preventDefault();
              e.stopPropagation();
              this.props.onLogoClick();
            } : () => {}}
          >
            {this.props.clientLogo ? <img src={this.props.clientLogo} alt={typeof this.props.clientName === 'string' ? this.props.clientName : 'Brand Logo'} style={{ width: '60%', height: 'auto' }} /> : this.props.clientName}
          </a>
        </div>
        <div className="register-box-body" style={{ position: 'relative' }}>
          <div>
            {error}
            <p className="register-box-msg">
              {this.props.headline}
              {this.props.subheadline ? (<small><br />{this.props.subheadline}</small>) : ''}
            </p>
          </div>
          <form>
            {this.props.hideNameField ? '' : (
              <div className="form-group has-feedback">
                <input
                  id="name"
                  name="name"
                  type="text"
                  className="form-control"
                  placeholder={this.props.namePlaceholder}
                  onChange={this.fillForm}
                />
                <span className="glyphicon glyphicon-user form-control-feedback" />
              </div>
            )}
            <div className="form-group has-feedback">
              <input
                id="username"
                name="username"
                type="text"
                className="form-control"
                placeholder={this.props.userPlaceholder}
                onChange={this.fillForm}
              />
              <span className="glyphicon glyphicon-envelope form-control-feedback" />
            </div>
            <div className="form-group has-feedback">
              <input
                id="password"
                name="password"
                type="password"
                className="form-control"
                placeholder={this.props.passPlaceholder}
                onChange={this.fillForm}
              />
              <span className="glyphicon glyphicon-lock form-control-feedback" />
            </div>
            {this.props.hasConfirmPass ? (
              <div className="form-group has-feedback">
                <input
                  id="confirm-password"
                  name="confirm-password"
                  type="password"
                  className="form-control"
                  placeholder={this.props.confirmPassPlaceholder}
                  onChange={this.fillForm}
                />
                <span className="glyphicon glyphicon-log-in form-control-feedback" />
              </div>
            ) : ''}
            {this.getBetterChildren(this.props.children)}
            {this.props.hasNoAgreement ? '' : (
              <div className="row">
                <div className="col-xs-12">
                  <Checkbox
                    id="agreement"
                    label={this.props.agreementText}
                    onChange={this.fillForm}
                    checked={this.state.agreement}
                  />
                </div>
              </div>
            )}
            <div className="row">
              <div className="col-xs-offset-8 col-xs-4">
                <button
                  type="submit"
                  className={`btn btn-block btn-flat ${this.props.submitTheme}`}
                  onClick={this.submitForm}
                  id="login-btn"
                >
                  {this.props.submitText}
                </button>
              </div>
            </div>
          </form>
          {this.props.socials.length > 0 ? (
            <div className="social-auth-links text-center">
              <p>- OR -</p>
              {this.props.socials}
            </div>
          ) : ''}
          {this.props.registered ? (
            <a
              className="text-center"
              href={this.props.registeredLink}
              onClick={this.props.onRegistered ? e => {
                e.preventDefault();
                e.stopPropagation();
                this.props.onRegistered();
              } : () => {}}
            >
              {this.props.registeredText}
            </a>
          ) : ''}
          {!this.props.loading ? '' : (
            <div
              className="text-center align-middle overlay over-load"
              style={{
                position: 'absolute',
                backgroundColor: 'rgba(236, 240, 245, 0.25)',
                top: '0', right: '0', bottom: '0', left: '0',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
              }}
            >
              <LoadStatus size="5em" color="rgba(51, 51, 51, 0.3)" spins />
            </div>
          )}
        </div>
      </div>
    );
  }
}

RegisterForm.propTypes = {
  clientLogo: PropTypes.string,
  clientName: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.element
  ]),
  logoLink: PropTypes.string,
  onLogoClick: PropTypes.func,
  headline: PropTypes.string,
  subheadline: PropTypes.string,
  registered: PropTypes.bool,
  socials: PropTypes.array,
  registeredLink: PropTypes.string,
  registeredText: PropTypes.string,
  onRegistered: PropTypes.func,
  submitTheme: PropTypes.string,
  submitText: PropTypes.string,
  userPlaceholder: PropTypes.string,
  namePlaceholder: PropTypes.string,
  passPlaceholder: PropTypes.string,
  hasConfirmPass: PropTypes.bool,
  confirmPassPlaceholder: PropTypes.string,
  agreementText: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.element
  ]),
  onSubmit: PropTypes.func,
  loading: PropTypes.bool,
  errorTheme: PropTypes.string,
  error: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.element
  ]),
  hideNameField: PropTypes.bool,
  hasNoAgreement: PropTypes.bool
};

RegisterForm.defaultProps = {
  clientLogo: '',
  clientName: <span><b>Admin</b>LTE</span>,
  headline: 'Register a new membership',
  subheadline: '',
  socials: [],
  registered: false,
  hasConfirmPass: false,
  registeredText: 'I already have a membership',
  submitTheme: 'btn-primary',
  submitText: 'Register',
  agreementText: 'I agree to the terms and policies',
  userPlaceholder: 'Username or Email',
  namePlaceholder: 'Full Name',
  passPlaceholder: 'Password',
  confirmPassPlaceholder: 'Confirm Password',
  onSubmit: () => {},
  loading: false,
  errorTheme: 'alert-danger',
  hideNameField: false,
  hasNoAgreement: false
};

export default RegisterForm;
